<template>
	<view class="container">
		<!-- 显示默认收货地址 -->
		<view class="address">
			<uni-list>
				<uni-list-item showArrow :title="addressInfo" :note="nameInfo" rightText="选择" :show-extra-icon="true"
					:extra-icon="Icon" :clickable="true" to="/pages/order/selectAddress" />
			</uni-list>
		</view>

		<!-- 显示订单详情 -->
		<view class="goods">
			<view class="item" v-for="g in goods" :key="g.cart_goods_id">
				<view class="left">
					<image :src="g.goods.image" mode="" class="img"></image>
					<view class="info">
						<view class="name">{{g.goods.title}}</view>
						<view class="number">{{g.goods.price+' x '+g.number+'件'}}</view>
					</view>
				</view>
				<view class="price">{{(g.goods.price*g.number).toFixed(2)}} 元</view>
			</view>

		</view>

		<!-- 提交订单的按钮 -->
		<view class="bot-box">
			<view class="price-box">
				<text class="text1">合计:</text>
				<text class="text2">￥</text>
				<text class="price">{{total.toFixed(2)}}元</text>
			</view>
			<button class="btn" @click="submitOrder">提交订单</button>
		</view>
	</view>
</template>

<script>
	import {
		mapState,
		mapGetters
	} from 'vuex'
	export default {
		data() {
			return {
				address: {},
				Icon: {
					color: '#4cd964',
					size: '22',
					type: 'location-filled'
				},
				goods: [],
				addrId:''
			}
		},
		computed: {
			...mapState(['cartGoodsList','addressList']),
			...mapGetters(['defaultAddress', 'total']),
			addressInfo() {
				return this.address.province + this.address.city + this.address.county + this.address.address
			},
			nameInfo() {
				return this.address.name + ' ' + this.address.mobile
			}
		},
		onLoad(options) {
		
		},
		methods: {
			// 获取购物车已选中的商品
			getGoodsInfo() {
				this.goods = this.cartGoodsList.filter((item) => {
					return item.is_checked == true
				})
			},
			// 创建订单
			async createOrder(data){
				const response = await this.$api.order.createOrder(data)
				if (response.data.code === 200) {
					uni.showToast({
						title:'提交成功',
						duration:1500
					})
					let orderId = response.data.data.order_code
					// 跳转支付页面
					let t = setTimeout(()=>{
						uni.navigateTo({
							url:`/pages/order/pay?id=${orderId}&&price=${this.total}`
						})
					},1500)
				}
			},
			// 提交订单
			async submitOrder() {
				const data = {
					address: this.addressInfo,
					amount: this.total,
					user_id: this.address.user_id,
					ordergoods_set: []
				}
				this.goods.forEach((item,index) => {
					let g = {
						goods: item.goods,
						number: item.number,
						price: item.goods.price
					}
					data.ordergoods_set.push(g)
					if(index==this.goods.length-1){
						this.createOrder(data)
					}
				})
			}
		},
		onShow() {
			this.getGoodsInfo()
			if(this.addrId){
				this.addressList.forEach((item)=>{
					if(item.id == this.addrId){
						this.address = item
					}
				})
			}else{
				this.address = this.defaultAddress
			}
		}
	}
</script>

<style lang="scss">
	.container {
		padding-bottom: 110upx;

		.goods {
			padding: 0 30upx 0 20upx;
			background-color: #fff;

			.item {
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 20upx 0;
				border-bottom: 1px solid #eaedef;

				&:last-child {
					border: 0;
				}

				.left {
					display: flex;
					align-items: center;

					.img {
						width: 150upx;
						height: 150upx;
					}

					.info {
						margin-left: 20upx;

						.name {
							font-size: 30upx;
						}

						.number {
							font-size: 26upx;
							color: #909399;
							margin-top: 10upx;
						}
					}
				}

				.price {
					font-size: 28upx;
					color: #ff557f;
				}
			}
		}

		.bot-box {
			width: 100%;
			position: fixed;
			left: 0;
			bottom: 0;
			height: 100upx;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 0 28upx;
			background-color: #F5F5F5;

			.price-box {
				.text1 {
					font-size: 24upx;
					margin-right: 10upx;
				}

				.text2 {
					font-size: 26upx;
					font-weight: bold;
					color: #ff557f;
				}

				.price {
					font-size: 32upx;
					font-weight: bold;
					color: #ff557f;
					font-family: '雅黑';
				}
			}

			.btn {
				width: 300upx;
				height: 75upx;
				line-height: 75upx;
				background-color: #009000;
				border-radius: 50upx;
				color: #fff;
				font-size: 35upx;
				margin-left: 20upx;
			}
		}
	}
</style>